*,
*:after,
*:before {
  box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.dg.ac {
  z-index: 99999 !important;
}

.preloader,
.preloader__slider,
.preloader__slide,
.not-found .page-cover,
.page-menu__card-bg,
.footer__bg,
.footer__bg img,
.footer__content,
.hero-section__bg,
.hero-section__bg img,
.timeline-section .scroll-container .parallax-item_img-cover img,
.timeline-section .scroll-container .parallax-item__img img,
.incident-section__bg,
.incident-section .incident-item__image,
.incident-section .incident-item__hover {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.preloader__slide,
.preloader__calendar,
.preloader__button,
.timer__item-title,
.timer__item-date,
.not-found .page-cover__wrapper ._container,
.page-menu__card-index,
.incident-section .incident-item__line {
  display: flex;
  justify-content: center;
  align-items: center;
}

.burger div:nth-child(2),
.page-menu__card-bg-wrap,
.page-menu__card-title,
.footer__content .container,
.hero-section__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.page-menu__slider {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  z-index: 1;
}

.swiper-pointer-events.page-menu__slider {
  touch-action: pan-y;
}

.page-menu__slider .swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.page-menu__slider .swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.timeline-section .scroll-container .parallax-item__img-text,
.blog-section .blog-item .blog-container .media-column p {
  font-family: "Ramillas";
  font-size: 1.4rem;
  font-weight: 400;
