}

.preloader__button {
  font-family: "Ramillas";
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.224rem;
}

.logo {
  font-family: "Ramillas";
  font-size: 2rem;
  font-weight: 400;
  line-height: 2.4rem;
}

.page-menu__card-title,
.incident-section .incident-item__descr p {
  font-family: "Ramillas";
  font-size: 2.4rem;
  font-weight: 400;
  line-height: 3.12rem;
  letter-spacing: 0.02rem;
}

.preloader__title,
.blog-section .blog-item_bg .content_center .blog-container p {
  font-family: "Ramillas";
  font-size: 6.2rem;
  font-weight: 400;
  line-height: 8.06rem;
  letter-spacing: 0.02rem;
}

.timer__item:first-child .timer__item-date:after,
.timer__item:nth-child(2) .timer__item-date:after,
.timer__item-date,
.footer__content .container .box .social-link,
.hero-section .hero-composition__title,
.timeline-section .scroll-container__bg {
  font-family: "Ramillas";
  font-size: 12.4rem;
  font-weight: 400;
  line-height: 12.4rem;
}

.history-section .line .stroke span,
.history-section .line .fill span {
  font-family: "Ramillas";
  font-size: 9.6rem;
  font-weight: 400;
  line-height: 12.4rem;
}

.not-found h1 {
  font-family: "Ramillas";
  font-size: 22rem;
  font-weight: 400;
  line-height: 26.4rem;
}

.timer__item-title,
.footer__copyright .container span {
  font-family: "Manrope";
  font-size: 1.2rem;
  font-weight: 400;
  line-height: 1.92rem;
}

.lang-toggle__item,
.page-menu__card-index,
.footer__content .container .box span,
.hero-section .hero-composition__description,
.incident-section .incident-item__hover,
.blog-section .blog-item .blog-container .text-column p {
  font-family: "Manrope";
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 2.24rem;
}

.scroll-indicator span {
  font-family: "Manrope";
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.56rem;
}

.timeline-section .scroll-container .parallax-item_text p,
.history-section .content .text-item p,
.blog-section .blog-item_bg .content .blog-container,
.blog-section .blog-item .blog-container .text-column p:first-child {
  font-family: "Manrope";
  font-size: 2.4rem;
  font-weight: 400;
  line-height: 3.84rem;
}

._container {
  box-sizing: content-box;
  margin: 0 auto;
  max-width: 168.8rem;
  width: 100%;
  padding: 0 4.4rem;
}

@font-face {
  font-family: "Ramillas";
  src: url("../../fonts/ramillas-regular.woff2") format("woff2"), url("../../fonts/ramillas-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Manrope";
  src: url("../../fonts/manrope-regular.woff2") format("woff2"), url("../../fonts/manrope-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

html {
  font-size: 10px;
}

body {
  font-family: "Manrope";
  cursor: none;
  background: #9F1307;
  overscroll-behavior: none;
}

body * {
  cursor: none !important;
}

body._lock,
body.menu-lock {
  overflow: hidden !important;
}

a {
  color: inherit;
  outline: none;
  text-decoration: none;
}

.scroll-container,
.split-child {
  will-change: transform;
}

.split-parent {
  overflow: hidden;
}

.pin-spacer:not(:last-child) {
  height: 1px !important;
}

[data-src] {
  opacity: 0;
  transition: opacity 1.5s ease;
}

[data-src].loaded {
  opacity: 1;
}

.preloader {
  overflow: hidden;
  background: #9F1307;
  box-shadow: 0px 130px 160px rgba(0, 0, 0, 0.3);
  z-index: 9;
  user-select: none;
}

.preloader__wrapper {
  width: 100%;
  height: 100%;
}

.preloader__wrapper ._container {
  width: auto;
  height: 100%;
}

.preloader__bg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 100%;
}

.preloader__bg ._container {
  width: auto;
  height: 100%;
}

.preloader__bg ._container .preloader__bg-inner {
  position: relative;
  height: 100%;
}

.preloader__bg ._container .preloader__bg-inner img {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
}

.preloader__slider-wrapper {
  position: relative;
  height: 100%;
}

.preloader__slide {
  height: 100%;
  opacity: 0;
  pointer-events: none;
  user-select: none;
}

@keyframes animationEnd {
  to {
    opacity: 0;
  }
}

@keyframes animationEndSecond {
  to {
    opacity: 0;
  }
}

.preloader__slide._active {
  opacity: 1;
  transition: opacity 2s cubic-bezier(0.19, 1, 0.22, 1);
  pointer-events: auto;
  user-select: auto;
}

.preloader__slide:nth-child(1)._active .circle-calendar .first-circle {
  transform-origin: 50% 50%;
  animation-name: leftRotate;
  animation-duration: 4.5s;
  animation-delay: 1s;
  animation-timing-function: cubic-bezier(0.54, 0.01, 0.42, 0.96);
  animation-fill-mode: forwards;
  transform: rotate(30deg);
}

.preloader__slide:nth-child(1)._active .circle-calendar .second-circle {
  transform-origin: 50% 50%;
  animation-name: rightRotate;
  animation-duration: 4.5s;
  animation-delay: 0.8s;
  animation-timing-function: cubic-bezier(0.54, 0.01, 0.42, 0.96);
  animation-fill-mode: forwards;
  transform: rotate(-30deg);
}

.preloader__slide:nth-child(1)._active .circle-calendar .third-circle {
  transform-origin: 50% 50%;
  animation-name: leftRotate;
  animation-duration: 4.5s;
  animation-delay: 0.5s;
  animation-timing-function: cubic-bezier(0.54, 0.01, 0.42, 0.96);
  animation-fill-mode: forwards;
  transform: rotate(30deg);
}

@keyframes leftRotate {
  to {
    transform: rotate(360deg);
  }
}

@keyframes rightRotate {
  to {
    transform: rotate(-360deg);
  }
}

.preloader__slide:nth-child(3) .preloader__slide-content ._container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  margin: 0;
  width: 100%;
}

.preloader__calendar {
  width: 56.2rem;
  height: 56.2rem;
}

.preloader__calendar svg {
  max-width: 100%;
  max-height: 100%;
}

.preloader__title {
  max-width: 92rem;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  display: block;
  margin-bottom: 7.2rem;
  overflow: hidden;
}

.preloader__title > div {
  overflow: hidden;
}

.preloader__title span {
  display: block;
  overflow: hidden;
}

.preloader__button {
  width: 20.5rem;
  height: 8rem;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 6.1rem;
  position: relative;
  overflow: hidden;
}

.preloader__button span {
  position: relative;
}

.preloader__button:hover:before {
  transform: scale(1.2);
  filter: brightness(0.4);
}

.preloader__button:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  transition: transform 2s cubic-bezier(0.19, 1, 0.22, 1), filter 2s cubic-bezier(0.19, 1, 0.22, 1);
  background: url("../../images/button-bg.png"), linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url("../../images/button-bg.png"), #FFFFFF;
  background-size: cover;
  background-position: 70% 90%;
  filter: brightness(0.6);
}

.timer {
  width: 62.6rem;
}

.timer__wrapper {
  display: flex;
  width: 100%;
}

.timer__item {
  width: 100%;
}

.timer__item:first-child .timer__item-title {
  border-left: 1px solid rgba(255, 255, 255, 0.3);
}

.timer__item:first-child .timer__item-date:after {
  content: ':';
  color: #fff;
  position: absolute;
  right: -8%;
  top: 50%;
  transform: translateY(-50%);
}

.timer__item:nth-child(2) .timer__item-title {
  border-left: 1px solid rgba(255, 255, 255, 0.3);
  border-right: 1px solid rgba(255, 255, 255, 0.3);
}

.timer__item:nth-child(2) .timer__item-date:after {
  content: ':';
  color: #fff;
  position: absolute;
  right: -8%;
  top: 50%;
  transform: translateY(-50%);
}

.timer__item:last-child .timer__item-title {
  border-right: 1px solid rgba(255, 255, 255, 0.3);
}

.timer__item-title {
  height: 3.3rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  color: #fff;
  margin-bottom: 1.8rem;
}

.timer__item-title span {
  margin-bottom: 1.2rem;
}

.timer__item-date {
  position: relative;
  color: #fff;
}

.timer__item-date span {
  overflow: hidden;
}

.not-found h1 {
  color: #fff;
  margin-bottom: 1.6rem;
}

.not-found .header {
  pointer-events: none;
}

.not-found .page-cover {
  overflow: hidden;
  background: #9F1307;
}

.not-found .page-cover__wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 2;
}

.not-found .page-cover__wrapper ._container {
  width: auto;
  height: 100%;
  flex-direction: column;
}

.not-found .page-cover__bg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 100%;
}

.not-found .page-cover__bg ._container {
  width: auto;
  height: 100%;
}

.not-found .page-cover__bg ._container .page-cover__bg-inner {
  position: relative;
  height: 100%;
}

.not-found .page-cover__bg ._container .page-cover__bg-inner img {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
}

.scroll-indicator {
  position: fixed;
  left: 50%;
  transform: translate(-50%, 0);
  bottom: 6.4rem;
  z-index: 8;
}

.scroll-indicator.horizontal svg {
  transform: rotate(-90deg);
}

.scroll-indicator span {
  color: #fff;
  position: relative;
}

.scroll-indicator svg {
  margin-left: 1rem;
}

.c-cursor {
  position: fixed;
  pointer-events: none;
  z-index: 13;
  transform-origin: 50% 50%;
}

.c-cursor * {
  pointer-events: none;
}

.c-cursor .page-cursor {
  transform: translate(-50%, -50%) rotate(45deg);
  width: 14.4px;
  height: 14.4px;
  background: rgba(159, 19, 7, 0.8);
  border: 1px solid rgba(255, 255, 255, 0.4);
  transform-origin: 50% 50%;
}

.c-cursor__slider {
  pointer-events: none;
  transform: translate(-50%, -50%);
  display: none;
}

.c-cursor__slider .arrow {
  transform-origin: 50% 50%;
}

.c-cursor__slider.right .arrow {
  transform: rotate(-180deg);
}

.c-cursor.above-slider .page-cursor {
  display: none;
}

.c-cursor.above-slider .c-cursor__slider {
  display: block;
}

.noise {
  animation: noise 1.2s steps(3) infinite both;
  background: url("../images/noise.png");
  height: 200%;
  left: -50%;
  pointer-events: none;
  position: fixed;
  top: -50%;
  width: 200%;
  will-change: auto;
  z-index: 11;
  opacity: .08;
}

@keyframes noise {
  0% {
    transform: translate3d(1%, -1%, 0);
  }

  10% {
    transform: translate3d(-5%, -2%, 0);
  }

  20% {
    transform: translate3d(10%, 5%, 0);
  }

  30% {
    transform: translate3d(5%, -11%, 0);
  }

  40% {
    transform: translate3d(-12%, -5%, 0);
  }

  50% {
    transform: translate3d(10%, 9%, 0);
  }

  60% {
    transform: translate3d(15%, 0, 0);
  }

  70% {
    transform: translate3d(-10%, 8%, 0);
  }

  80% {
    transform: translate3d(10%, 2%, 0);
  }

  90% {
    transform: translate3d(1%, 5%, 0);
  }

  100% {
    transform: translate3d(0, 8%, 0);
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 12;
  color: #fff;
  height: 8rem;
  display: flex;
  align-items: center;
}

.header.menu-open .logo {
  color: #fff !important;
}

.header.menu-open .lang-toggle {
  border-color: #fff !important;
}

.header.menu-open .lang-toggle__item {
  color: #fff !important;
}

.header.dark-theme .logo {
  color: #000;
}

.header.dark-theme .lang-toggle {
  border-color: #000;
}

.header.dark-theme .lang-toggle__item {
  color: #000;
}

.header.dark-theme .burger div {
  background: #000;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.logo {
  transition: color 1s cubic-bezier(0.19, 1, 0.22, 1);
  color: #fff;
  text-transform: uppercase;
  white-space: nowrap;
  letter-spacing: 0.02rem;
}

.burger {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  transition: opacity 1s cubic-bezier(0.19, 1, 0.22, 1);
}

.burger.disable {
  opacity: 0.3;
  pointer-events: none;
  user-select: none;
}

.burger._active div:first-child {
  opacity: 0;
}

.burger._active div:nth-child(2) {
  opacity: 1;
}

.burger._active div:last-child {
  opacity: 0;
}

.burger div {
  transition: opacity 0.8s cubic-bezier(0.19, 1, 0.22, 1), background 1s cubic-bezier(0.19, 1, 0.22, 1);
  width: 5.6rem;
  background: #fff;
}

.burger div:first-child {
  margin-bottom: 5px;
  height: 1px;
}

.burger div:nth-child(2) {
  height: 3px;
  opacity: 0;
}

.burger div:last-child {
  height: 3px;
}

.lang-toggle {
  border-left: 2px solid #fff;
  padding-left: .6rem;
  display: flex;
}

.lang-toggle:hover .lang-toggle__item:not(._active) {
  opacity: 0.4;
}

.lang-toggle__item {
  transition: opacity 1s cubic-bezier(0.19, 1, 0.22, 1), color 1s cubic-bezier(0.19, 1, 0.22, 1);
  opacity: 0;
  color: #fff;
}

.lang-toggle__item._active {
  opacity: 1;
}

.lang-toggle__item:not(:last-child) {
  margin-right: 1.2rem;
}

.page-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  z-index: 10;
}

.page-menu__wrapper {
  height: 100%;
  padding: 14.4rem 0;
}

.page-menu__wrapper ._container {
  height: 100%;
  width: auto;
}

.page-menu__slider {
  overflow: visible;
  width: 100%;
  min-width: 0;
  color: #fff;
}

.page-menu__slider-controls {
  display: flex;
  align-items: center;
  margin-top: 4rem;
}

.page-menu__slider-controls .swiper-button-prev.swiper-button-disabled svg,
.page-menu__slider-controls .swiper-button-next.swiper-button-disabled svg {
  fill: #fff;
}

.page-menu__slider-controls .swiper-button-next {
  transform: rotate(180deg);
}

.page-menu__slider-controls .swiper-scrollbar {
  width: 100%;
  height: 5px;
  background: rgba(255, 255, 255, 0.1);
  margin: 0 2.4rem;
}

.page-menu__slider-controls .swiper-scrollbar div {
  background: #fff;
  height: 5px;
}

.page-menu__card {
  position: relative;
  width: 100%;
  height: 35.5rem;
  background: #161616;
  padding: 1.6rem;
}

.page-menu__card .img-top path,
.page-menu__card .img-bottom path {
  transition: fill 2s cubic-bezier(0.19, 1, 0.22, 1);
}

.page-menu__card-bg {
  overflow: hidden;
}

.page-menu__card-bg-wrap {
  width: 100%;
}

.page-menu__card-bg-wrap .img-top,
.page-menu__card-bg-wrap .img-bottom {
  max-width: 100%;
}

.page-menu__card-bg-wrap .img-top svg,
.page-menu__card-bg-wrap .img-bottom svg {
  transition: transform 1.4s cubic-bezier(0.19, 1, 0.22, 1);
}

.page-menu__card-bg-wrap .img-top path,
.page-menu__card-bg-wrap .img-bottom path {
  transition: fill 1.4s cubic-bezier(0.19, 1, 0.22, 1);
}

.page-menu__card-content {
  position: relative;
  height: 100%;
  z-index: 2;
}

.page-menu__card-title {
  width: 100%;
  text-align: center;
}

.dots {
  position: relative;
  display: block;
  width: 22px;
  height: 6px;
  margin-left: 0.5rem;
  margin-bottom: 1.2rem;
}

.dots span:first-child,
.dots span:last-child {
  content: '';
  width: 6px;
  height: 6px;
  background: #fff;
  position: absolute;
  top: 0;
  border-radius: 100%;
}

.dots span:first-child {
  left: 0;
}

.dots span:last-child {
  right: 0;
}

.footer {
  color: #fff;
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  top: 0 !important;
}

.footer.is-view .split-child {
  will-change: transform;
  transform: translateY(0);
  opacity: 1;
}

.footer .split-child {
  transition: transform 2s cubic-bezier(0.19, 1, 0.22, 1), opacity 2s cubic-bezier(0.19, 1, 0.22, 1);
  transform: translateY(100%);
  opacity: 0;
}

.footer__wrapper {
  position: relative;
